import { Component, OnInit } from '@angular/core';
import { AccountsService } from 'src/app/api/controllers/Accounts';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';

@Component({
	selector: 'app-income-tracker-setup-general',
	templateUrl: './income-tracker-setup-general.component.html',
	styleUrls: ['./income-tracker-setup-general.component.scss']
})
export class IncomeTrackerSetupGeneralComponent implements OnInit {

	
	isError: boolean = false;
	errorMessage: boolean = false;

	currencyTypeDataList: any;

	glAccountListData: any;
	glAccountIndicator: any = "Income";

	customerIncomeName:any;
	customerIncomeLookupValueId:any;

	customerVatName:any;
	customerVatLookupValueId:any;

	customerTaxName:any;
	customerTaxLookupValueId:any;

	custIncomePaymentlist: any;
	vatPecentageDataList: any;
	taxPecentageDataList: any;

	currencyTypeId:string = "";
	glaccountId:string = "";

	public config: PerfectScrollbarConfigInterface = {};

	constructor(private accountsService: AccountsService,
		private lookupService: LookupService,
		private sharedService: SharedService,
		private cookieService: CookieService) {

	}

	addCustAccount(){
		
		var data = this.glAccountListData.filter(item => {
			return item.glaccountId == this.glaccountId
		})

		let details = {
			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"glaccountId": parseInt(this.glaccountId),
			"comment": data[0].glaccountName,
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString(),
			"updatedBy": null,
			"updatedOn": null
		}

		let params = {
			custAccount: details
		}

		this.accountsService.addCustAccount(params).subscribe((res:any) => {
			if(res.message){
				this.sharedService.setAlertMessage("Gl Account added successfully");
			}
		})

	}
	

	isCustomerIncomePayment() {
		return this.customerIncomeName == null ? 'disabled' : ''
	}

	isCustomerVAT() {
		return this.customerVatName == null ? 'disabled' : ''
	}

	isCustomerTAX() {
		return this.customerTaxName == null ? 'disabled' : ''
	}

	getCustomerIncomePayment() {
		
		let custPayParams = {
			ApartmentId: parseInt(this.cookieService.get('apartmentId')),
			LookupTypeId: 74
		}

		this.lookupService.getLookupValueByLookupTypeId(custPayParams).subscribe((res: any) => {

			this.custIncomePaymentlist = res.filter(item => {
				return item.isActive;
			});
			
		});
	}

	clearCustomerIncome() {
		this.customerIncomeName = null;
		this.customerIncomeLookupValueId = null;
	}

	checkCustomerIncome() {
		if(this.customerIncomeName == null){
			this.customerIncomeLookupValueId = null;
		}
	}

	submitCustomerIncomePayment() {

		let details= {
			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"lookupTypeId": 74,
			"lookupValueName": `${this.customerIncomeName}`,
			"description": `${this.customerIncomeName}`,
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString(),
			"updatedBy": null,
			"updatedOn": null,
			"menuName": "string",
			"notes": "string",
			"isCommon": false,
			"module": "string",
			"subModule": "string",
			"isDisabled": true
		}

		let params = {
			lookupvalue: details
		}

		this.lookupService.addLookupValue(params).subscribe((res:any) => {
			this.clearCustomerIncome();
			this.sharedService.setAlertMessage("Customer Income Payment Term added successfully");
			this.getCustomerIncomePayment();
		})
	}

	getCustomerVat() {
		//VAT
		let vatListParams = {
			ApartmentId: parseInt(this.cookieService.get('apartmentId')),
			LookupTypeId: 77
		}

		this.lookupService.getLookupValueByLookupTypeId(vatListParams).subscribe((res: any) => {

			this.vatPecentageDataList = res.filter(item => {
				return item.isActive;
			});
		});
	}

	clearCustomerVat() {
		this.customerVatName = null;
		this.customerVatLookupValueId = null;
	}

	checkCustomerVat() {
		if(this.customerVatName == null){
			this.customerVatLookupValueId = null;
		}
	}

	submitCustomerVat() {

		let details= {
			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"lookupTypeId": 77,
			"lookupValueName": `${this.customerVatName}`,
			"description": `${this.customerVatName}`,
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString(),
			"updatedBy": null,
			"updatedOn": null,
			"menuName": "string",
			"notes": "string",
			"isCommon": false,
			"module": "string",
			"subModule": "string",
			"isDisabled": true
		}

		let params = {
			lookupvalue: details
		}

		this.lookupService.addLookupValue(params).subscribe((res:any) => {
			this.clearCustomerVat();
			this.sharedService.setAlertMessage("Customer Vat added successfully");
			this.getCustomerVat();
		})
	}

	getCustomerTax() {
		//Tax
		let taxListParams = {
			ApartmentId: parseInt(this.cookieService.get('apartmentId')),
			LookupTypeId: 183
		}

		this.lookupService.getLookupValueByLookupTypeId(taxListParams).subscribe((res: any) => {

			this.taxPecentageDataList = res.filter(item => {
				return item.isActive;
			});
		});
	}

	clearCustomerTax() {
		this.customerTaxName = null;
		this.customerTaxLookupValueId = null;
	}

	checkCustomerTax() {
		if(this.customerTaxName == null){
			this.customerTaxLookupValueId = null;
		}
	}

	submitCustomerTax() {

		let details= {
			"apartmentId": parseInt(this.cookieService.get('apartmentId')),
			"lookupTypeId": 183,
			"lookupValueName": `${this.customerTaxName}`,
			"description": `${this.customerTaxName}`,
			"isActive": true,
			"insertedBy": parseInt(this.cookieService.get('userId')),
			"insertedOn": new Date().toISOString(),
			"updatedBy": null,
			"updatedOn": null,
			"menuName": "string",
			"notes": "string",
			"isCommon": false,
			"module": "string",
			"subModule": "string",
			"isDisabled": true
		}

		let params = {
			lookupvalue: details
		}

		this.lookupService.addLookupValue(params).subscribe((res:any) => {
			this.clearCustomerTax();
			this.sharedService.setAlertMessage("Customer Tax added successfully");
			this.getCustomerTax();
		})
	}


	deleteLookupValueItem(event, item, type) {

		event.stopPropagation();

		let details = {
			lookupValueId: item.lookupValueId,
			updateUserId: parseInt(this.cookieService.get('userId'))
		}

		this.lookupService.deleteLookupvalue(details).subscribe((res:any) => {

			if(res.message){
				if(type == 'income')
					this.getCustomerIncomePayment()
				else if(type == "vat")
					this.getCustomerVat()
				else
					this.getCustomerTax()
			}
		})

	}
	
	ngOnInit(): void {

		this.getCustomerIncomePayment();
		this.getCustomerVat();
		this.getCustomerTax();


		//currency type
		let params = {
			ApartmentId: parseInt(this.cookieService.get('apartmentId')),
			LookupTypeId: 101
		}

		this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {

			this.currencyTypeDataList = res.filter(item => {
				return item.isActive;
			});
			this.currencyTypeId = this.currencyTypeDataList[0].lookupValueId;

		});

		//GLAccountTypeId
		let glAccountTypeIdparams = {
			apartmentId: parseInt(this.cookieService.get('apartmentId')),
			GLAccountTypeId: 165
		}

		this.accountsService.getGlAccountsIdbyGlAccountTypeId(glAccountTypeIdparams).subscribe((res: any) => {

			this.glAccountListData = res.filter(item => {
				return item.isActive && parseInt(this.cookieService.get('apartmentId')) && item.indicator == this.glAccountIndicator;
			});
			this.glaccountId = this.glAccountListData[0].glaccountId;
		});

	}

	
}
